	module.exports = {"data":{"markdownRemark":{"html":"<p>自己在路由器上跑了个不稳定版本的 OpenWrt，再加上 Netgear 4300 的性能也一般，在上面跑 shadowsocks，\n过段时间还是需要重启下的，每次重启都需要重新设定一次路由规则，所以要经常链接路由 SSH，但每次都要输密码就很繁琐，不过 OpenWrt 也是基于 linux 的开源版本，肯定是能通过 ssh key 免密码登陆的。<!--more--></p>\n<h2>本地生成 key</h2>\n<p>打开用户根目录创建 .ssh 目录，并生成 key( 之前有创建过请忽略)</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-shell\"><code>cd ~/ mkdir .ssh && cd .ssh\nssh-keygen -t dsa</code></pre>\n      </div>\n<h2>上传 key</h2>\n<p>把本地生成的 key 上传到 OpenWrt，同时创建authorized_key</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-shell\"><code>scp ~/.ssh/id_dsa.pub root@192.168.1.1:/tmp\nssh root@192.168.1.1\ncd /etc/dropbear\ncat /tmp/id_*.pub >> authorized_keys\nchmod 0600 authorized_keys</code></pre>\n      </div>","excerpt":"自己在路由器上跑了个不稳定版本的 OpenWrt，再加上 Netgear 4300 的性能也一般，在上面跑 shadowsocks，\n过段时间还是需要重启下的，每次重启都需要重新设定一次路由规则，所以要经常链接路由 SSH…","fields":{"slug":"/ebb536cf72","tags":[{"name":"openwrt","slug":"27d03b6f0f"},{"name":"ssh","slug":"1787d76463"}]},"frontmatter":{"date":"2014-08-19","path":"/openwrt-authorized-key","title":"使用 ssh key 登陆 OpenWrt"}},"site":{"siteMetadata":{"disqusShortName":"devharry","siteUrl":"https://goharry.io"}}},"pathContext":{"slug":"/ebb536cf72","prev":{"excerpt":"supervisor 是基于 python 的任务管理工具，用来自动运行各种后台任务，当然你也能直接利用 nohup 命令使任务自动后台运行，但如果要重启任务，每次都自己手动 kill…","fields":{"slug":"/ab5bd5df67","tags":[{"name":"python","slug":"23eeeb4347"},{"name":"virtualenv","slug":"d613f49da0"},{"name":"supervisor","slug":"09348c20a0"}]},"frontmatter":{"path":"/setup-supervisor-based-on-virtualenv","title":"配置基于 virtualenv 的 supervisor","date":"2014-08-24T00:00:00.000Z"}},"next":{"excerpt":"来深圳后体重不断的增加，从当初60公斤左右到现在已经是超重了，脂肪也在身上越来越多，前段时间体检出血压偏高和中度的脂肪肝，所以减肥是不可避免了。为了更科学的计量每天的运动量，就入手了这款智能手环 fibit flex 外观 fitbit…","fields":{"slug":"/7457b2b5fb","tags":[{"name":"fitbit","slug":"ca82ed41cd"}]},"frontmatter":{"path":"/review-of-fitbit-flex","title":"fitbit flex 使用体验","date":"2014-07-19T00:00:00.000Z"}}}}

